<template>
  <HeaderHero
    class="backgroundImage"
    title="Contact Us"
    subtitle=""
    :backgroundImage="backgroundImage"
  />
  <div class="container">
    <div class="row">
      <div class="form col-lg-12">
        <h2>Feel free to get in touch with any questions you have</h2>
      </div>
      <div class="form col-lg-6">
        <form @submit="sendEmail">
          <div class="row">
            <label for="name">Name:</label>
            <input type="text" id="name" v-model="name" required />
          </div>
          <div class="row">
            <label for="email">Email:</label>
            <input type="email" id="email" v-model="email" required />
          </div>
          <div class="row">
            <label for="number">Phone Number:</label>
            <input id="number" v-model="number" />
          </div>
          <div class="row">
            <label for="message">Message:</label>
            <textarea id="message" v-model="message" required></textarea>
          </div>
          <button
            type="submit"
            data-bs-toggle="modal"
            data-bs-target="#exampleModal"
          >
            Send
          </button>
        </form>
      </div>
      <div class="col-lg-6">
        <div class="card">
          <img
            src="https://a0.muscache.com/im/pictures/miso/Hosting-53084565/original/eb42d9fb-5383-4c75-b9f2-80d0891b9424.jpeg?im_w=1440"
            class="card img-fluid"
          />
        </div>
      </div>
    </div>

    <div>
      <div
        class="modal fade"
        id="exampleModal"
        tabindex="-1"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div class="modal-dialog">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title" id="exampleModalLabel">
                Message Sent Successfully
              </h5>
              <button
                type="button"
                class="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>
          </div>
          <div class="modal-body">
            Thankyou for your message, we will be in touch as soon as possible
          </div>
          <div class="modal-footer"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import emailjs from "emailjs-com";
import HeaderHero from "../components/HeaderHero.vue";

export default {
  components: { HeaderHero },
  data() {
    return {
      backgroundImage: require("../assets/Boonah.jpg"),
      name: "",
      email: "",
      number: "",
      message: "",
    };
  },
  methods: {
    sendEmail(event) {
      event.preventDefault();
      emailjs
        .send(
          "service_782v726",
          "template_4wt4phr",
          {
            from_email: this.email,
            name: this.name,
            email: this.email,
            number: this.number,
            message: this.message,
          },
          "7SreG1RwN4Y_f0fzK"
        )
        .then(
          (response) => {
            console.log("SUCCESS!", response.status, response.text);
            this.showModal = true;
          },
          function (error) {
            console.log("FAILED...", error);
            this.showModal = false;
          }
        );
    },
  },
};
</script>

